@import "custom";

.weather-card-container {
  display: flex;
  justify-content: flex-start;
  align-items: stretch;
  gap: 0.5em;
  overflow-x: scroll;
  scroll-behavior: smooth;
  -webkit-overflow-scrolling: touch;

  .temp {
    margin-top: auto;
  }
}

.weather-card-container > * {
  flex-shrink: 0;
  width: 10ch;
  padding: 1em 0;
  text-align: center;
  background-color: $clr-lighter-dark;
  border-radius: 0.75em;

  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  gap: 3px;
}
